.print {
  background: #fff;
  font-family: "Roboto", sans-serif;
}
.print-line {
  background: transparent;
  width: 100%;
  border: none;
  border-bottom: solid 1px #000;
}
.print-2rem {
  margin-top: 2rem;
}
.print-2rem-bottom {
  margin-bottom: 2rem;
}
.print-justify {
  text-align: justify;
}
.print-italic {
  font-style: italic;
}
.print-underline {
  text-decoration: underline;
}
.print-cell {
  display: table-cell;
}
.print-text {
  font-style: italic;
  font-weight: 500;
  font-size: 11pt;
}
.print-list {
  font-size: 10pt;
}
.print ul {
  list-style-type: none !important;
}

.print-2 {
  background: #fff;
  font-family: "Roboto", sans-serif;
}
.print-2-line {
  background: transparent;
  width: 100%;
  border: none;
  border-bottom: solid 1px #000;
}
.print-2-2rem {
  margin-top: 2rem;
}
.print-2-2rem-bottom {
  margin-bottom: 2.5rem;
}
.print-2-6rem {
  margin-top: 6rem;
}
.print-2-10rem {
  margin-top: 10rem;
}
.print-2-justify {
  text-align: justify;
}
.print-2-italic {
  font-style: italic;
}
.print-2-underline {
  text-decoration: underline;
}
.print-2-cell {
  display: table-cell;
}
.print-2-text {
  font-style: italic;
  font-weight: 500;
  font-size: 14pt;
}
.print-2-list {
  font-size: 13pt;
}
.print-2-14 {
  font-size: 14pt !important;
}
.print-2 ul {
  list-style-type: none !important;
}

@media print {
  body {
    width: 100% !important;
    margin: 0 !important;
    padding: 0 !important;
    line-height: 1.45;
    color: #000;
    background: none;
    font-size: 12pt;
    overflow: hidden !important;
  }
  /* Headings */
  h1,
h2,
h3,
h4,
h5,
h6 {
    page-break-after: avoid;
  }
  h1 {
    font-size: 19pt;
  }
  h2 {
    font-size: 17pt;
  }
  h3 {
    font-size: 15pt;
  }
  h4 {
    font-size: 14pt;
  }
  h5 {
    font-size: 13pt;
  }
  h6 {
    font-size: 12pt;
  }
  p {
    font-size: 14pt;
  }
  p,
h2,
h3 {
    orphans: 3;
    widows: 3;
  }
  code {
    font: 12pt Courier, monospace;
  }
  blockquote {
    margin: 1.2em;
    padding: 1em;
    font-size: 12pt;
  }
  hr {
    background-color: #ccc;
  }
  /* Images */
  img {
    float: left;
    margin: 1em 1.5em 1.5em 0;
    max-width: 100% !important;
  }
  a img {
    border: none;
  }
  /* Links */
  /* a:link,
  a:visited {
      background: transparent;
      font-weight: 700;
      text-decoration: underline;
      color: #333;
  }
  a:link[href^="http://"]:after, a[href^="http://"]:visited:after
  {
      content: " (" attr(href) ") ";
      font-size: 90%;
  } */
  abbr[title]:after {
    content: " (" attr(title) ")";
  }
  /* Don't show linked images  */
  /* a[href^="http://"]
  {
      color: #000;
  }
  a[href$=".jpg"]:after,
  a[href$=".jpeg"]:after,
  a[href$=".gif"]:after,
  a[href$=".png"]:after {
      content: " (" attr(href) ") ";
      display: none;
  } */
  /* Don't show links that are fragment identifiers, or use the `javascript:` pseudo protocol .. taken from html5boilerplate */
  /* a[href^="#"]:after,
  a[href^="javascript:"]:after {
      content: "";
  } */
  table {
    border: solid #333 !important;
    border-width: 1px 0 0 1px !important;
  }
  th,
td {
    border: solid #333 !important;
    border-width: 0 1px 1px 0 !important;
  }
  /* Table */
  table {
    margin: 1px;
    text-align: left;
    overflow: hidden !important;
    overflow-x: hidden !important;
    overflow-y: hidden !important;
  }
  .table-responsive {
    overflow: hidden !important;
    overflow-x: hidden !important;
    overflow-y: hidden !important;
  }
  th {
    border-bottom: 1px solid #333;
    font-weight: bold;
  }
  td {
    border-bottom: 1px solid #333;
  }
  td span {
    font-size: 18px !important;
    padding: 0 !important;
    color: #000 !important;
  }
  th,
td {
    font-size: 18px;
    padding: 4px 10px 4px 0;
    color: #000 !important;
  }
  tfoot {
    font-style: italic;
  }
  caption {
    background: #fff;
    margin-bottom: 2em;
    text-align: left;
  }
  thead {
    display: table-header-group;
  }
  img,
tr {
    page-break-inside: avoid;
  }
  .card-body,
.content,
.container,
.container-fluid,
.content-wrapper {
    padding: 0 !important;
    margin: 0 !important;
  }
  .card-header,
.card-footer,
.card-send,
.header-mobile,
.aside,
.footer,
.header,
.subheader {
    display: none !important;
  }
  .font-weight-bold {
    font-weight: 500;
  }
  ul {
    list-style-type: none !important;
  }
}